  --white: #fff;
  --black: #111;
  --html-bg: rgb(244, 244, 244);
  --wrapper-bg: #fee6e3;
}

@font-face {
  font-family: Philosopher;
  src: url(${___CSS_LOADER_URL_REPLACEMENT_0___});
}
* {
  font-family: "Philosopher", sans-serif;
  box-sizing: border-box;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-rendering: geometricPrecision;
  outline: 0;
  -moz-outline-style: none;
  -webkit-tap-highlight-color: transparent;
  outline-style: auto;
}

body,
html {
  background-color: var(--html-bg);
  height: 100%;
  overflow: hidden;
}

h1 {
  font-size: 2.5em;
}

.section-quote {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.wrapper {
  background-color: var(--wrapper-bg);
  border: 2px solid var(--black);
  border-radius: 8px;
  box-sizing: border-box;
  color: var(--black);
  max-width: 100%;
  padding: 0 25px;
  position: relative;
}
.wrapper undefined:after {
  background-color: var(--black);
  border-radius: 8px;
  content: "";
  display: block;
  height: 100%;
  left: 0;
  width: 100%;
  position: absolute;
  top: -2px;
  transform: translate(8px, 8px);
  transition: transform 0.2s ease-out;
  z-index: -1;
}

.keyboard {
  display: flex;
  justify-content: center;
  align-items: center;
}

.quote-letters-count {
  margin-bottom: 20px;
}

.badge {
  padding: 4px 8px;
  text-align: center;
  border-radius: 5px;
  margin-left: 4px;
  color: var(--white);
  background-color: var(--black);
}

.info-wrapper {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.start-btn-wrapper {
  display: flex;
  justify-content: center;
  align-items: center;
}

.timer {
  margin-top: 20px;
}

button {
  background-color: var(--black);
  border-radius: 4px;
  border-style: none;
  color: var(--white);
  cursor: pointer;
  font-size: 16px;
  font-weight: 700;
  max-width: none;
  min-height: 44px;
  min-width: 10px;
  margin-bottom: 20px;
  outline: none;
  overflow: hidden;
  padding: 9px 20px 8px;
  position: relative;
  text-align: center;
  text-transform: none;
  user-select: none;
}
button undefined:hover,
button undefined:focus {
  opacity: 0.75;
}

.opacity-on {
  opacity: 1;
  transition: opacity 1s ease-out;
}

.opacity-off {
  opacity: 0;
}`, "",{"version":3,"sources":["webpack://./src/components/index.scss"],"names":[],"mappings":"AAAA;EACC,aAAA;EACA,aAAA;EACA,6BAAA;EACA,qBAAA;AACD;;AAEA;EACC,wBAAA;EACA,4CAAA;AACD;AAGA;EACC,sCAAA;EACA,sBAAA;EACA,mCAAA;EACA,kCAAA;EACA,kCAAA;EACA,UAAA;EACA,wBAAA;EACA,wCAAA;EACA,mBAAA;AADD;;AAIA;;EAEC,gCAAA;EACA,YAAA;EACC,gBAAA;AADF;;AAIA;EACC,gBAAA;AADD;;AAIA;EACC,kBAAA;EACA,QAAA;EACA,SAAA;EACA,gCAAA;AADD;;AAIA;EACC,mCAAA;EACA,8BAAA;EACA,kBAAA;EACA,sBAAA;EACA,mBAAA;EACA,eAAA;EACA,eAAA;EACA,kBAAA;AADD;AAGC;EACC,8BAAA;EACA,kBAAA;EACA,WAAA;EACA,cAAA;EACA,YAAA;EACA,OAAA;EACA,WAAA;EACA,kBAAA;EACA,SAAA;EACA,8BAAA;EACA,mCAAA;EACA,WAAA;AADF;;AAKA;EACC,aAAA;EACA,uBAAA;EACA,mBAAA;AAFD;;AAKA;EACC,mBAAA;AAFD;;AAKA;EACC,gBAAA;EACA,kBAAA;EACA,kBAAA;EACA,gBAAA;EACA,mBAAA;EACA,8BAAA;AAFD;;AAKA;EACC,aAAA;EACA,8BAAA;EACA,mBAAA;AAFD;;AAKA;EACC,aAAA;EACA,uBAAA;EACA,mBAAA;AAFD;;AAKA;EACC,gBAAA;AAFD;;AAKA;EACC,8BAAA;EACA,kBAAA;EACA,kBAAA;EACA,mBAAA;EACA,eAAA;EACA,eAAA;EACA,gBAAA;EACA,eAAA;EACA,gBAAA;EACA,eAAA;EACA,mBAAA;EACA,aAAA;EACA,gBAAA;EACA,qBAAA;EACA,kBAAA;EACA,kBAAA;EACA,oBAAA;EACA,iBAAA;AAFD;AAGC;;EAEC,aAAA;AADF;;AAKA;EACC,UAAA;EACA,+BAAA;AAFD;;AAKA;EACC,UAAA;AAFD","sourcesContent":[":root {\r\n\t--white: #fff;\r\n\t--black: #111;\r\n\t--html-bg: rgb(244, 244, 244);\r\n\t--wrapper-bg: #fee6e3;\r\n}\r\n\r\n@font-face {\r\n\tfont-family: Philosopher;\r\n\tsrc: url(../);\r\n\r\n}\r\n\r\n* {\r\n\tfont-family: \"Philosopher\", sans-serif;\r\n\tbox-sizing: border-box;\r\n\t-webkit-font-smoothing: antialiased;\r\n\t-moz-osx-font-smoothing: grayscale;\r\n\ttext-rendering: geometricPrecision;\r\n\toutline: 0;\r\n\t-moz-outline-style: none;\r\n\t-webkit-tap-highlight-color: transparent;\r\n\toutline-style: auto;\r\n}\r\n\r\nbody,\r\nhtml {\r\n\tbackground-color: var(--html-bg);\r\n\theight: 100%;\r\n\t\toverflow: hidden;\r\n}\r\n\r\nh1 {\r\n\tfont-size: 2.5em;\r\n}\r\n\r\n.section-quote {\r\n\tposition: absolute;\r\n\ttop: 50%;\r\n\tleft: 50%;\r\n\ttransform: translate(-50%, -50%);\r\n}\r\n\